#root {
  height: 100vh;
  display: grid;
  grid-template-rows: 2em 1fr;
}

header {
  white-space: nowrap;
  font-family: 'Indie Flower', serif;
  font-size: 1.3em;
}

main {
  display: grid;
  grid-template-columns: 8em 1fr;
}

.button-bar > button {
  margin: 0.5em;
  display: block;
  width: 50%;
}
